/* ===============================================================
   MIXINS
   - Custom mixins that are not available in Bootstrap.
   - Available Bootstrap mixins can be found in:
     ../node_modules/bootstrap/scss/mixins
   - _vendor-prefixes.scss in above has a lot of handy mixins
   =============================================================== */
/* ==============================================================
   BASE
   - Styles for default/global HTML elements (ex. body, a, h1-h6)
   - ALWAYS check to see if you can achieve your styles first by
     overriding bootstrap variables
   - MISC items can also go here (not ideal)
============================================================== */
/* Smoother fonts */
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* Responsive images */
img {
  max-width: 100%;
  height: auto;
  width: auto; }

/* Responsive Video */
iframe,
video {
  max-width: 100%; }

/* Make oembeds responsive */
.entry-oembed {
  position: relative;
  padding-bottom: 56.25%;
  overflow: hidden;
  max-width: 100%;
  height: auto; }
  .entry-oembed iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

/* Headings */
h1:first-child, .h1:first-child,
h2:first-child,
.h2:first-child,
h3:first-child,
.h3:first-child,
h4:first-child,
.h4:first-child,
h5:first-child,
.h5:first-child,
h6:first-child,
.h6:first-child {
  margin-top: 0; }

/* Paragraphs */
p {
  margin-bottom: 1em; }

