/* Small */
.padding-sm {
  padding: 20px; }

.padding-sm-top {
  padding-top: 20px; }

.padding-sm-right {
  padding-right: 20px; }

.padding-sm-bottom {
  padding-bottom: 20px; }

.padding-sm-left {
  padding-left: 20px; }

.padding-sm-vert {
  padding-top: 20px;
  padding-bottom: 20px; }

.padding-sm-horz {
  padding-left: 20px;
  padding-right: 20px; }

/* Default */
.padding {
  padding: 40px; }

.padding-top {
  padding-top: 40px; }

.padding-right {
  padding-right: 40px; }

.padding-bottom {
  padding-bottom: 40px; }

.padding-left {
  padding-left: 40px; }

.padding-vert {
  padding-top: 40px;
  padding-bottom: 40px; }

.padding-horz {
  padding-left: 40px;
  padding-right: 40px; }

/* Medium */
.padding-md {
  padding: 80px; }

.padding-md-top {
  padding-top: 80px; }

.padding-md-right {
  padding-right: 80px; }

.padding-md-bottom {
  padding-bottom: 80px; }

.padding-md-left {
  padding-left: 80px; }

.padding-md-vert {
  padding-top: 80px;
  padding-bottom: 80px; }

.padding-md-horz {
  padding-left: 80px;
  padding-right: 80px; }

/* Large */
.padding-lg {
  padding: 100px; }

.padding-lg-top {
  padding-top: 100px; }

.padding-lg-right {
  padding-right: 100px; }

.padding-lg-bottom {
  padding-bottom: 100px; }

.padding-lg-left {
  padding-left: 100px; }

.padding-lg-vert {
  padding-top: 100px;
  padding-bottom: 100px; }

.padding-lg-horz {
  padding-left: 100px;
  padding-right: 100px; }

/* Padding for Responsive Vertical Stacking Containers */
.stacked-space {
  padding-top: 40px; }

.stacked-space.stacked-space-xs {
  padding-top: 0; }

@media (min-width: 576px) {
  .stacked-space.stacked-space-sm {
    padding-top: 0; } }

@media (min-width: 768px) {
  .stacked-space.stacked-space-md {
    padding-top: 0; } }

@media (min-width: 992px) {
  .stacked-space.stacked-space-lg {
    padding-top: 0; } }

/* ===============================================================
    TYPE
	- Styles that can be applied to text
	- The intended real world use for these selectors is for
	  creating a quick, nice looking page (that has not been provided
	  with a design) in Drupal via the wysiwyg editor.
	- When creating a page that DOES have a design, it is preferable
      to assign variables to the elements rather than use these classes.
	- ** RENAME colo selectors with your custom color
	  variable names
   =============================================================== */
/* Text Color */
.txt-brand-primary,
.txt-purple {
  color: #0d6efd; }

.txt-brand-success,
.txt-magenta {
  color: #198754; }

.txt-brand-info,
.txt-lilac {
  color: #0dcaf0; }

.txt-brand-warning,
.txt-orange {
  color: #ffc107; }

.txt-brand-danger,
.txt-pink {
  color: #dc3545; }

.txt-white {
  color: #fff; }

/* Text Color - Links */
a.txt-brand-primary:hover,
a.txt-purple:hover {
  color: #024dbc; }

a.txt-brand-success:hover,
a.txt-magenta:hover {
  color: #0d462c; }

a.txt-brand-info:hover,
a.txt-lilac:hover {
  color: #098da7; }

a.txt-brand-warning:hover,
a.txt-orange:hover {
  color: #ba8b00; }

a.txt-brand-danger:hover,
a.txt-pink:hover {
  color: #a71d2a; }

/* Font Weights */
.weight-light {
  font-weight: 300; }

.weight-normal {
  font-weight: 400; }

.weight-bold {
  font-weight: 700; }

.txt-sm {
  font-size: 0.875rem; }

.txt-base {
  font-size: 1rem; }

.txt-lg {
  font-size: 1.25rem; }

/* Font Families */
.font-sans-serif {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; }

.font-serif {
  font-family: Georgia, "Times New Roman", Times, serif; }

/* Line Heights */
.line-height-sm {
  line-height: 1.1; }

.line-height-base {
  line-height: 1.42857; }

/* ===============================================================
    Forms
	- Global form styles and Bootstrap form overrides
   =============================================================== */
img.alignright {
  float: right;
  margin: 0 0 15px 15px; }

img.alignleft {
  float: left;
  margin: 0 15px 15px 0; }

img.aligncenter {
  margin: 0 auto;
  display: block; }
